.project-card {
  background-color: #c1c4e5;
  color: #1f3a50;
  display: flex;
  flex-direction: column;
  /* width: 450px; */
  width: 100%;
  overflow: hidden;
  border-radius: 4em;
}

.project-card-image {
  width: 100%;
  height: 320px;
}

.project-card-title {
  font-size: 2em;
}

.project-card-info {
  margin: 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
}

.project-card-tools {
  font-size: 1.5em;
}

.project-card-date {
  font-size: 1.2em;
}

.project-card-description {
  font-size: 1.2em;
}

.fa-icon {
  width: 100%;
  height: auto;
}

a {
  text-align: center;
  width: 80px;
}

.project-icon-container {
  display: flex;
  justify-content: center;
}