
.skill-card {
  display: inline-flex;
  align-items: center;
  background-color: #274C77;
  color: #e7ecef;
  padding: 1.2em 1.5em;
  border-radius: 2em;
  min-width: 10em;
  transition: transform 0.3s;
  margin: 2em 1em;
}

.skill-card:hover {
  transform: scale(1.1); /* Enlarge the div by 20% */
}

.skill-card-logo {
  width: 4em;
  height: 4em;
  margin-right: 1em;
}

