.portfolio--experience {
  min-height: 100vh;
  background: linear-gradient(to bottom, #333333, #555555);
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1.5em;
}

.portfolio--experience-title {
  font-size: 4em;
  letter-spacing: 0.1em;
  padding: 0.2em 0.2em;
  border-radius: 0.1em;
  background-color: #338983;
  color: #cccee3;
  margin: 0.5em;
}

.portfolio--experience-content {
  width: 70%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media (max-width: 1400px) {
  .portfolio--experience-content {
    width: 80%;
  }
}

@media (max-width: 1200px) {
  .portfolio--experience-content {
    width: 85%;
  }
}

@media (max-width: 900px) {
  .portfolio--experience-content {
    width: 90%;
  }
}